@font-face {
  font-family: bodoni;
  src: url("./fonts/bodoni72.woff") format('woff');
}

body {
  background-color: black;
  color: white;
  position: relative;
  font-family: bodoni;
}

h2, h1, h3, h4, h5, h6 {
  font-weight: normal;
}

header {
  margin-top: 50px;
}

.d-flex {
  display: flex;
}

.align-items-center {
  align-items: center;
}

.flex-column {
  flex-direction: column;
}

.justify-content-center {
  justify-content: center;
}

h2 {
  font-family: 'Bodoni';
}

.h-p {
  font-family: 'Nunito', sans-serif;
  max-width: 600px;
  text-align: center;
  font-size: 18px;
}

h3, span {
  font-family: 'Nunito', sans-serif;
}

.m-container {
  max-width: 1050px;
  flex-wrap: wrap;
  justify-content: center;
}

.mt-0 {
  margin-top: 0px;
}

.card-span {
  margin-top: 55px;
  color: #9D9D9D;
}

.card-button {
  width: 219px;
  height: 45px;
  font-size: 20px;
  color: black;
  margin-top: 10px;
  border: none;
}

.bg-image {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.bg-image img {
  width: 100%;
  height: 100%;
  animation: opa 5s linear infinite;
}

@keyframes opa {
  0% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

.card h2.mt-0 {
  text-align: center;
  padding: 0 35px;
  font-size: 30px;
  font-style: italic;
}

.balance-card .b-title {
  margin-top: 20px;
  margin-bottom: 0px;
}

.balance-card hr {
  width: 328px;
}

.mt-50 {
  margin-top: 50px;
}

.balance-card .b-text {
  width: 300px;
  justify-content: space-between;
}

.balance-button {
  width: 280px;
  height: 70px;
  font-size: 20px;
  margin-top: 50px;
}

.h-title p {
  font-size: 60px;
  font-family: "bodoni";
  margin: 0.3em;
}

.card-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  bottom: 40px;
}

.card-img {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 600px) {
  .h-logo {
      text-align: center;
  }
  .h-logo img {
      width: 70%;
  }
  .h-title p {
      font-size: 40px !important;
      text-align: center;
      margin: 0.6em !important;
  }
  
  .balance-card {
      width: 333px;
      height: 197px;
  }

  .balance-card h1 {
      margin-bottom: 0px;
  }

  .balance-card hr{
      width: 251px;
  }

  .balance-card .b-text {
      width: 246px;
  }


}

.bg-image {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;    
}

.bg-image img {
  height: unset;
}